import { useEffect, useState } from "react";
import DataTable from "./components/DataTable";
import "./styles.css";

function App() {
  const [data, setData] = useState([]);
  const [query, setQuery] = useState("");
  const [sortValue, setSortValue] = useState("username");

  const fetchData = () => {
    fetch(
      `https://jsonplaceholder.typicode.com/users?q=${query}&_sort=${sortValue}`
    )
      .then((response) => response.json())
      .then((result) => setData(result))
      .catch((error) => alert("error", error));
  };

  const handleReset = () => {
    setQuery("");
    fetchData();
  };

  const handleSort = (e) => {
    const newValue = e.target.value;
    setSortValue(newValue);
  };

  useEffect(() => {
    fetchData();
  }, [query, sortValue]);

  return (
    <main className="App">
      <h1>Simple Table App</h1>

      <input
        type="text"
        placeholder="Search data"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
      />
      <button onClick={handleReset}>Clear</button>

      <label htmlFor="sort">Sort by:</label>
      <select name="sort" value={sortValue} onChange={handleSort}>
        <option value="name">Full Name</option>
        <option value="username">Username</option>
        <option value="city">City</option>
      </select>

      <DataTable input={data} />
    </main>
  );
}

export default App;
