const DataTable = (props) => {
  return (
    <table>
      <thead>
        <tr>
          <th>Full Name</th>
          <th>User Name</th>
          <th>E-mail</th>
          <th>City</th>
        </tr>
      </thead>
      <tbody>
        {props.input.length === 0 ? (
          <tr>
            <td>No Data</td>
          </tr>
        ) : (
          props.input.map((datum) => {
            return (
              <tr key={datum.id}>
                <td>{datum.name}</td>
                <td>{datum.username}</td>
                <td>{datum.email}</td>
                <td>{datum.address.city}</td>
              </tr>
            );
          })
        )}
      </tbody>
    </table>
  );
};

export default DataTable;
